import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core'
import { MatLegacyFormFieldAppearance as MatFormFieldAppearance } from '@angular/material/legacy-form-field'
import { AutoDestroy } from '@core/common/auto-destroy'
import { debounceTime, takeUntil } from 'rxjs'
import { FormControl } from '@angular/forms'

@Component({
  selector: 'app-auto-complete',
  templateUrl: './auto-complete.component.html',
  styleUrls: ['./auto-complete.component.scss']
})
export class AutoCompleteComponent extends AutoDestroy implements OnInit, OnChanges {

  @Input()
  public label: string

  @Input()
  public placeholder: string

  @Input()
  public control: FormControl<string>

  @Input()
  public appearance: MatFormFieldAppearance = 'fill'

  @Input()
  public options: { label: any, value: any }[]

  @Input()
  public loading: boolean = false

  private initialOptions: typeof this.options = []

  searchForm = new FormControl()

  ngOnChanges(changes: SimpleChanges) {
    const optionsChange = changes['options']
    if (optionsChange) {
      this.initialOptions = optionsChange.currentValue
      this.searchForm.setValue(this.control.getRawValue())
    }
  }

  ngOnInit() {
    // let initialOptions = this.options;
    this.searchForm.valueChanges.pipe(takeUntil(this.destroy$), debounceTime(150)).subscribe((term: any) => {
      // @todo might need to watch for changes in option lists (data.options)
      // if (initialOptions?.length === 0) {
      //   initialOptions = this.options
      // }
      // @todo how to avoid searching for integer value?
      // term = ""+term;
      if (term && !term.trim) {
        return
      }
      this.options = this.initialOptions?.filter(
        (attr) => {
          return attr.label.trim().toLowerCase().search(term?.trim().toLowerCase()) >= 0
        }
      )
    })
  }

  displayFn = (optionId: any) => {
    const option = this.options.find((option) => option.value === optionId)
    return option && option.label ? option.label : ''
  }

}
