export class Address {
  id?: number
  profile?: ProfileType
  addressType?: AddressType
  firstName?: string
  lastName?: string
  email?: string
  companyName?: string
  addressLine1?: string
  addressLine2?: string
  country?: string
  city?: string
  region?: string
  phoneNumber?: {
    value: string,
    locale: string
  }
  vatNumber?: string
  zipCode?: string
  featured?: boolean
  defaultAddress?: boolean

  fullAddress(separater = " ") {
    return [this.addressLine1, this.city, this.region].filter(x => !!x).join(separater)
  }

  toDto() {
    return { ...this, ...{ street: this.addressLine1, county: this.region } }
  }

  public static adapt(item: Partial<Address>|any): Address {
    const addr: Address = new Address()
    addr.id = item?.id
    addr.companyName = item?.companyName
    addr.vatNumber = item?.vatNumber
    addr.firstName = item?.firstName
    addr.phoneNumber = item?.phoneNumber
    addr.country = item?.country
    addr.region = item?.region || item?.county
    addr.city = item?.city
    addr.addressLine1 = item?.addressLine1 || item?.street
    addr.addressLine2 = item?.addressLine1
    addr.zipCode = item?.zipCode
    addr.featured = item?.featured
    addr.defaultAddress = item?.defaultAddress

    return addr
  }

}

export enum ProfileType {
  Personal = 'PERSONAL',
  Company = 'COMPANY'
}

export enum AddressType {
  DELIVERY = "DELIVERY",
  BILLING = "BILLING"
}
