import {Component, Inject, OnInit} from '@angular/core'
import {MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef} from '@angular/material/legacy-dialog'
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms'
import {Address, ProfileType} from '@core/models/address.model'
import {ApiService} from "@core/services/api.service";
import {MicroserviceType} from "@core/enums/microservice-type.enum";
import {City, Country, Region} from "@core/models/types";
import {finalize} from "rxjs";

@Component({
  selector: 'app-edit-address',
  templateUrl: 'edit.component.html',
  styleUrls: ['edit.component.scss']
})
export class EditAddressComponent implements OnInit {

  addressProfiles = [
    { label: 'Personal', 'value': ProfileType.Personal },
    { label: 'Company', 'value': ProfileType.Company },
  ]

  public profileType = ProfileType
  public countries: { label: string; value: number }[] = []
  public regions: Array<{ label: string, value: number }> = []
  public cities: Array<{ label: string, value: number }> = []
  public form: FormGroup<AddressFormGroup>;
  public loading = { state: false, city: false, country: false }

  constructor(
    private api: ApiService,
    private dialogRef: MatDialogRef<EditAddressComponent>,

    fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: {address: Address, mode: string}
  ) {
    // super()
    // this._initFormControl(fb, {})


    this.form = fb.group({
      profile: [data.address.profile || ProfileType.Personal, Validators.required],
      companyName: [data.address.companyName],
      vatNumber: [data.address.vatNumber],
      addressLine1: [data.address.addressLine1, Validators.required],
      country: [data.address.country, Validators.required],
      region: [data.address.region, Validators.required],
      city: [data.address.city, Validators.required],
      zipCode: [data.address.zipCode, Validators.required],
    }) as typeof this.form;


    data.address.profile = data.address.profile || ProfileType.Personal

    this.form.controls['country']?.valueChanges.subscribe((value) => {
      this.form.controls['region']?.reset()
      this.fetchRegions()
    })
    this.form.controls['region']!.valueChanges.subscribe((value) => {
      this.form.controls['city']?.reset()
      this.fetchCities()
    })
  }

  ngOnInit() {
    this.fetchCountries()
    this.fetchRegions()
    this.fetchCities()
  }

  cancel() {
    this.dialogRef.close(null)
  }

  private fetchCountries() {
    this.loading['country'] = true
    this.api.get<Array<Country>>('location/countries', MicroserviceType.usr)
    // this.api.get('countries')
      .pipe(finalize(() => this.loading['country'] = false))
      .subscribe((countries: Country[]) => {
        this.countries = countries.map((country) => ({ label: country.name, value: country.id }))
      })
  }

  private fetchRegions() {
    const countryId = this.form.controls['country']?.value
    if (countryId) {
      this.loading['state'] = true
      this.api.get(`location/counties/${countryId}`, MicroserviceType.usr)
        .pipe(finalize(() => this.loading['state'] = false))
        .subscribe((regions: Array<Region>) => this.regions = regions.map((region) => ({ label: region.name, value: region.id })))
    }
  }

  private fetchCities() {
    const regionId = this.form.controls['region']?.value
    if (regionId) {
      this.loading['city'] = true
      this.api.get(`location/cities/${regionId}`, MicroserviceType.usr)
        .pipe(finalize(() => this.loading['city'] = false))
        .subscribe((cities: Array<City>) => this.cities = cities.map((city) => ({ label: city.name, value: city.id })))
    }
  }

  save() {
    this.form.markAllAsTouched()
    if (this.form.invalid) {
      return
    }

    Object.keys(this.form?.value).forEach((k) => {
      const val = this.form?.controls[k as keyof AddressFormGroup]?.value
      this.data.address[k as keyof Address] = val as never
    })

    this.dialogRef.close(this.data.address)
  }
}

type AddressFormGroup = {
  [key in keyof Omit<Address, 'fullAddress'|'toDto'>] : FormControl<any>
}
