import { AbstractControl, FormGroup, ValidationErrors, ValidatorFn } from '@angular/forms'

export class FormValidators {
  static match1(otherField: string, form: FormGroup|null): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      // const sourceCtrl = control.get(source)
      // const otherCtrl = control.get(otherField)
      debugger
      const otherCtrl = form?.controls[otherField]

      if (otherCtrl?.value !== control.value) {
        control?.setErrors({ match: true })
      } else {
        control?.setErrors(null)
      }

      return null
    }
  }

  static match(source: string, target: string): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const sourceCtrl = control.get(source)
      const targetCtrl = control.get(target)

      if (sourceCtrl?.value !== targetCtrl?.value) {
        targetCtrl?.setErrors({ not_match: true })
      } else {
        targetCtrl?.setErrors(null)
      }

      return null
    }
  }

  public static passwordStrength(control: AbstractControl): ValidationErrors | null {
    let value: string = control.value || ''

    if (!value) {
      return null
    }

    let numberCharacters = /[0-9]+/g
    if (numberCharacters.test(value) === false) {
      return { number_characters: true }
    }

    return null
  }

  public static whitespace(control: AbstractControl): ValidationErrors | null {
    const hasWhitespace = (control.value || '').trim().length === 0
    const isValid = !hasWhitespace
    return isValid ? null : { whitespace: true }
  }
}
